/* Nunito Sans Font Family */
@font-face {
  font-display: swap;
  font-family: "Nunito Sans";
  font-style: normal;
  font-weight: 400;
  src: url("./Fonts/NunitoSans_7pt-Regular.ttf") format("truetype");
}

@font-face {
  font-display: swap;
  font-family: "Nunito Sans";
  font-style: normal;
  font-weight: 500;
  src: url("./Fonts/NunitoSans_7pt-Medium.ttf") format("truetype");
}

@font-face {
  font-display: swap;
  font-family: "Nunito Sans";
  font-style: normal;
  font-weight: 600;
  src: url("./Fonts/NunitoSans_7pt-SemiBold.ttf") format("truetype");
}

@font-face {
  font-display: swap;
  font-family: "Nunito Sans";
  font-style: normal;
  font-weight: 700;
  src: url("./Fonts/NunitoSans_7pt-Bold.ttf") format("truetype");
}

/* Modern font stack with fallbacks */
:root {
  --font-primary: "Nunito Sans", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Inter", "SF Pro Display", system-ui, sans-serif;
  --font-fallback: -apple-system, BlinkMacSystemFont, "Segoe UI", "Inter",
    "Helvetica Neue", Arial, sans-serif;
}
